import React, { useState } from 'react';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import CircularProgress from '@material-ui/core/CircularProgress';
import { v4 as uuidv4 } from 'uuid';

const App = () => {
  const [amazone, setAmazon] = useState([])
  const [ebay, setEbay] = useState([])
  const [search, setSearch] = useState('')
  const [progress, setProgress] = useState(false)


  const getProducts = async () => {
    if (search.length > 0) {
      fetch(`/${search}`)
        .then(res => res.json())
        .then(data => {
          if (data) {
            setAmazon(data.amazon)
            setEbay(data.ebay)
          }
        })
    }
  };


  const classes = useStyles()
  return (
    <React.Fragment>
      <CssBaseline />
      <AppBar position="relative">
        <Toolbar>
          <Typography variant="h6" color="inherit" noWrap>
            Give me ebay and Amazone
          </Typography>
        </Toolbar>
      </AppBar>
      <main>
        {/* Hero unit */}
        <div className={classes.heroContent}>
          <Container maxWidth="sm">
            <TextField fullWidth value={search} id="search" label="Search for items..." variant="outlined" placeholder="Search"
              onChange={e => setSearch(e.target.value)} required />
            <Button fullWidth type="submit" variant="contained" size="large" color="primary" style={{ marginTop: 10 }} onClick={() => {
              setAmazon([])
              setEbay([])
              setSearch('')
              getProducts()
              setProgress(search.length > 0 ? true : false)
            }}>Search</Button>
          </Container>
        </div>
        {!amazone.length && !ebay.length && progress ? (
          <div className={classes.progress}>
            <CircularProgress />
          </div>

        ) : (
            <Container className={classes.cardGrid} maxWidth="md">
              <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                Amazone
               </Typography>
              <Grid container spacing={4}>
                {amazone.length > 1 ? amazone.map((p) => (
                  <Grid item key={uuidv4()} xs={12} sm={6} md={4}>
                    <Card className={classes.card}>
                      <CardMedia
                        className={classes.cardMedia}
                        image={p.img}
                        title={p.name}
                      />
                      <CardContent className={classes.cardContent}>
                        <Typography gutterBottom variant="h5" component="h2">
                          {p.price}
                        </Typography>
                        <Typography>
                          {p.name}
                        </Typography>
                      </CardContent>
                      <CardActions>
                        <Link href={p.url} target="_blank" >
                          Link
                     </Link>
                      </CardActions>
                    </Card>
                  </Grid>
                )) : null}
              </Grid>
              <hr />
              <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                Ebay
               </Typography>
              <Grid container spacing={4}>
                {ebay.length > 1 ? ebay.map((p) => (
                  <Grid item key={uuidv4()} xs={12} sm={6} md={4}>
                    <Card className={classes.card}>
                      <CardMedia
                        className={classes.cardMedia}
                        image={p.img}
                        title={p.name}
                      />
                      <CardContent className={classes.cardContent}>
                        <Typography gutterBottom variant="h5" component="h2">
                          {p.price}
                        </Typography>
                        <Typography>
                          {p.name}
                        </Typography>
                      </CardContent>
                      <CardActions>
                        <Link href={p.url} >
                          Link
                     </Link>
                      </CardActions>
                    </Card>
                  </Grid>
                )) : null}
              </Grid>
            </Container>
          )}
      </main>
    </React.Fragment>
  );
}

const useStyles = makeStyles((theme) => ({
  heroContent: {
    padding: theme.spacing(8, 0, 6),
  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {
    paddingTop: '56.25%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  progress: {
    marginLeft: theme.spacing(2),
    display: 'flex',
    justifyContent: 'center'
  },
}));

export default App